.flight-card {
    background-color: #fff;
    -webkit-box-shadow: 0 0 5px rgba(0,0,0,0.2);
            box-shadow: 0 0 5px rgba(0,0,0,0.2);
    border-radius: 5px;
    margin-bottom: 20px;
    padding: 10px;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-wrap: wrap;
        flex-wrap: wrap;
    -ms-flex-pack: justify;
        justify-content: space-between;
}

.flight-card__item {
    width: 40%;
}

.flight-card__item--right {
    text-align: right;
}

.flight-card__title {
    font-weight: 700;
    font-size: 22px;
    line-height: 1.8;
}

.flight-card__carrier {
    width: 100%;
    text-align: center;
    background-color: #f7f7f7;
    padding: 10px 0;
    margin-top: 20px;
    font-weight: 700;
    font-size: 16px;
}


