.container {
  max-width: 600px;
  padding: 0.1px 50px;
  margin: 0 auto;
}

h1 {
  text-align: center;
  margin: 0 0 1em;
  font-size: 40px;
}

.header {
  margin-top: 40px;
}

.flights-select {
  margin-bottom: 40px;
  text-align: center;
}
.flights-select__select {
  height: 30px;
  border: 1px solid #cfcfcf;
  padding: 0 15px;
  font-size: 16px;
  display: inline-block;
  width: 100%;
  max-width: 200px;
}
.flights-select__label {
  color: #b9b9b9;
  font-size: 16px;
  margin-bottom: 5px;
  display: block;
  text-align: center;
}

.flights-list {
  margin-bottom: 50px;
}


